import React from 'react';
import PropTypes from 'prop-types';
import RaisedButton from 'material-ui/RaisedButton';

class Modal extends React.Component {
  render() {
    // Render nothing if the "show" prop is false
    if(!this.props.show) {
      return null;
    }

    // The gray background
    const backdropStyle = {
      position: 'fixed',
      top: 0,
      bottom: 0,
      left: 0,
      right: 0,
      backgroundColor: 'rgba(0,0,0,0.7)',
      padding: 50
    };

    // The modal "window"
    const modalStyle = {
      backgroundColor: '#4cb7c5',
      borderRadius: 10,
      maxWidth: 500,
      minHeight: 300,
      margin: 'auto',
      marginTop: 30,
      padding: 30
    };

    return (
      <div className="backdrop" style={backdropStyle}>
        <div className="modal" style={modalStyle}>
          {this.props.children}
          <br /> <br />
          <div style={{margin: "auto"}}>
            <RaisedButton label="Close" labelStyle={{ fontWeight: 'bold'}}  onClick={this.props.onClose} />
          </div>
        </div>
      </div>
    );
  }
}

Modal.propTypes = {
  onClose: PropTypes.func.isRequired,
  show: PropTypes.bool,
  children: PropTypes.node
};

export default Modal;



